.generate-cv .title {
    font-size: 1.1rem;
    font-weight: 500;
    padding: .5rem 0;
}

.generate-cv button {
    padding: .4rem;
    border-radius: 5%;
}

.appcolor {
    background-color: #a3979e !important;
}

.card-size {
    width: 62vw;
    margin: auto;
}

.nav-link.active {
    padding: 12px;
}

.card-size-list {
    width: 62vw;
    margin: auto;
    padding: 18px;
}

.content-container {
    width: 85%;
    margin: auto;
    padding-top: 30px;
}

.vl {
    border-left: 3px solid #a3979e;
    height: 200px;
}

.card-padding {
    padding-left: 2vw;
}

.upload-btn {
    background-color: #6FCF97;
    border: none;
}

.upload-btn:hover {
    background-color: #52df8d !important;
}

.generate-cv .cv-header {
    font-family: serif;
}

.generate-cv .template-size {
    height: 140px;
    width: 100px;
}


/* Media Query Section */

@media screen and (max-width: 767px) {
    .small-screen {
        display: none;
    }
    .card-size {
        width: 90%;
        margin: auto;
    }
    .card-size-list {
        width: 100%;
    }
    .vl {
        border-left: 3px solid #a3979e;
        height: 317px;
    }
}